"use client"

import { Input } from "@/components/ui/input";
import { useState } from "react";

const Login = ({action}) => {
const [userName,setUsername]=useState("")
const [password, setPassword] = useState("");
  return (
    <form
      action={action}
      className="w-80 p-3 bg-secondary rounded-sm text-white"
    >
      <h1 className="text-2xl mb-4">تسجيل دخول</h1>
      <div className="inputs ">
        <div className="grid w-full max-w-sm items-center gap-1.5 mb-3">
          <label htmlFor="username">اسم المستخدم</label>
          <Input
            type={"text"}
            value={userName}
            onChange={(event) => setUsername(event.target.value)}
            placeplaceholder="UserName"
            name={"userName"}
            className={"text-slate-950 "}
          />
        </div>
        <div className="grid w-full max-w-sm items-center gap-1.5 ">
          <label htmlFor="username">كلمة المرور</label>
          <Input
            type={"text"}
            value={password}
            onChange={(event) => setPassword(event.target.value)}
            placeplaceholder="Password"
            name={"password"}
            className={"text-slate-950 "}
          />
        </div>
      </div>
      <button>stubmit</button>
    </form>
  );
};

export default Login;
